import { useState, useCallback } from 'react';
import useSWR, { mutate } from 'swr';
import {
  Button,
  Container,
  Typography,
  Stack,
  Paper,
  Table,
  TableHead,
  TableBody,
  TableRow,
  TableCell,
  IconButton,
  CircularProgress,
} from '@mui/material';
import dayjs from 'dayjs';
import { Edit, Delete } from '@mui/icons-material';
import axiosInstance, { endpoints } from 'src/utils/axios';
import MasterDrawer from 'src/components/drawer';
import ReusableDeletePopover from 'src/components/delete-popup/delete-popup';
import { useSettingsContext } from 'src/components/settings';
import InvoiceForm from './InvoiceForm';

const fetcher = (url) => axiosInstance.get(url).then((res) => res.data);

export default function InvoiceView() {
  const settings = useSettingsContext();
  const [openDrawer, setOpenDrawer] = useState(false);
  const [editInvoice, setEditInvoice] = useState(null);
  console.log('ðŸš€ ~ InvoiceView ~ editInvoice:', editInvoice);
  const [deletePopoverEl, setDeletePopoverEl] = useState(null);
  const [selectedInvoiceId, setSelectedInvoiceId] = useState(null);

  const { data: invoices, error, isLoading } = useSWR(endpoints.invoice.list, fetcher);
  const { data: customers, erro, isLoadig } = useSWR(endpoints.customer.list, fetcher);
  const { data: itemsList, errr, isLading } = useSWR(endpoints.items.list, fetcher);

  const handleOpenDeletePopover = (event, id) => {
    setDeletePopoverEl(event.currentTarget);
    setSelectedInvoiceId(id);
  };

  const handleCloseDeletePopover = () => {
    setDeletePopoverEl(null);
    setSelectedInvoiceId(null);
  };

  const handleConfirmDelete = () => {
    handleDelete(selectedInvoiceId);
    handleCloseDeletePopover();
  };

  const handleAdd = useCallback(() => {
    setEditInvoice(null);
    setOpenDrawer(true);
  }, []);

  const handleSaveInvoice = useCallback(
    async (data) => {
      try {
        let response;

        if (editInvoice) {
          response = await axiosInstance.put(`/invoice/update/${editInvoice._id}`, data);
          const updated = invoices.map((inv) =>
            inv._id === editInvoice._id ? response.data : inv
          );
          mutate(endpoints.invoice.list, updated, false);
        } else {
          response = await axiosInstance.post('/invoice/add', data);
          mutate(endpoints.invoice.list, [...invoices, response.data], false);
        }

        setOpenDrawer(false);
      } catch (err) {
        console.error('Failed to save invoice:', err);
      }
    },
    [invoices, editInvoice]
  );

  const handleDelete = useCallback(
    async (id) => {
      try {
        await axiosInstance.delete(`/invoice/delete/${id}`);
        mutate(endpoints.invoice.list);
      } catch (err) {
        console.error('Failed to delete invoice:', err);
      }
    },
    [] // No dependencies needed
  );

  if (error) {
    return (
      <Container>
        <Typography variant="h4">Invoices</Typography>
        <p style={{ color: 'red', paddingTop: 100 }}>Failed to load invoices.</p>
      </Container>
    );
  }

  // Fetch customers and items
  // useEffect(() => {
  //   const fetchCustomers = async () => {
  //     const data = await fetcher(endpoints.customer.list);
  //     setCustomers(data);
  //   };
  //   const fetchItems = async () => {
  //     const data = await fetcher(endpoints.items.list);
  //     setItemsList(data);
  //   };
  //   fetchCustomers();
  //   fetchItems();
  // }, []);

  return (
    <Container maxWidth={settings.themeStretch ? false : 'xl'}>
      <Stack direction="row" justifyContent="space-between" mb={3}>
        <Typography variant="h4">Invoices</Typography>
        <Button variant="contained" onClick={handleAdd}>
          Add Invoice
        </Button>
      </Stack>

      <Paper>
        {isLoading ? (
          <Stack alignItems="center" py={3}>
            <CircularProgress />
          </Stack>
        ) : (
          <Table>
            <TableHead>
              <TableRow>
                <TableCell>Customer</TableCell>
                <TableCell>Sales Person</TableCell>
                <TableCell>Invoice Date</TableCell>
                <TableCell>Final Amount</TableCell>
                <TableCell align="right">Actions</TableCell>
              </TableRow>
            </TableHead>
            <TableBody>
              {invoices?.length > 0 ? (
                invoices.map((invoice) => (
                  <TableRow key={invoice._id}>
                    <TableCell>{invoice.customerId?.name}</TableCell>
                    <TableCell>{invoice.sales_person}</TableCell>
                    <TableCell>{new Date(invoice.invoice_date).toLocaleDateString()}</TableCell>
                    <TableCell>â‚¹{invoice.final_amount.toFixed(2)}</TableCell>
                    <TableCell align="right">
                      <IconButton
                        color="primary"
                        onClick={() => {
                          const invoiceD = {
                            _id:invoice._id,
                            customerId: invoice.customerId._id,
                            sales_person: invoice.sales_person,
                            invoice_date: dayjs(invoice.invoice_date),
                            delivery_date: dayjs(invoice.delivery_date),
                            description: invoice.description,
                            items: invoice.items.map((itemObj) => ({
                              item: itemObj.item._id,
                              quantity: itemObj.quantity,
                              rate: itemObj.rate,
                              tax: itemObj.tax,
                              total: itemObj.total,
                            })),
                            discount: invoice.discount,
                          };
                          setEditInvoice(invoiceD);
                          setOpenDrawer(true);
                        }}
                      >
                        <Edit />
                      </IconButton>
                      <IconButton
                        color="error"
                        onClick={(e) => handleOpenDeletePopover(e, invoice._id)}
                      >
                        <Delete />
                      </IconButton>
                    </TableCell>
                  </TableRow>
                ))
              ) : (
                <TableRow>
                  <TableCell colSpan={5} align="center" sx={{ py: 4 }}>
                    <Typography variant="body1">No invoices available.</Typography>
                  </TableCell>
                </TableRow>
              )}
            </TableBody>
          </Table>
        )}
      </Paper>

      <MasterDrawer
        open={openDrawer}
        onClose={() => setOpenDrawer(false)}
        title={editInvoice ? 'Update Invoice' : 'Add Invoice'}
      >
        <InvoiceForm
          initialData={editInvoice || {}}
          onSubmit={handleSaveInvoice}
          onClose={() => setOpenDrawer(false)}
          customers={customers}
          itemsList={itemsList}
        />
      </MasterDrawer>

      <ReusableDeletePopover
        open={deletePopoverEl}
        onClose={handleCloseDeletePopover}
        onConfirm={handleConfirmDelete}
        title="Delete Invoice?"
        description="Are you sure you want to delete this invoice?"
      />
    </Container>
  );
}
